let OrderListComponent = class OrderListComponent extends BaseListComponent {
    constructor(dataService, router, route) {
        super(router, route);
        this.dataService = dataService;
        this.searchTerm = new FormControl('');
        this.stateFilter = new FormControl('all');
        super.setQueryFn((...args) => this.dataService.order.getOrders(...args), data => data.orders, (skip, take) => {
            const stateFilter = this.stateFilter.value;
            const state = stateFilter === 'all' ? null : { eq: stateFilter };
            return {
                options: {
                    skip,
                    take,
                    filter: {
                        code: {
                            contains: this.searchTerm.value,
                        },
                        state,
                    },
                    sort: {
                        updatedAt: SortOrder.DESC,
                    },
                },
            };
        });
    }
    ngOnInit() {
        super.ngOnInit();
        merge(this.searchTerm.valueChanges, this.stateFilter.valueChanges)
            .pipe(debounceTime(250), takeUntil(this.destroy$))
            .subscribe(() => this.refresh());
    }
};
